import React from 'react'
import loginService from './services/loginService'
import registerService from './services/registerService'
import {useState} from 'react'

const App = () => {
    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [user,setUser] = useState(null)
    
    const handleRegistration = async (event) => {
        console.log('Handling Registration Now!')

        const userObject = {
            name,
            email,
            username,
            password,
        }

        setUser(userObject)
    
        const response = await registerService.register(user)
        console.log('Registration successful', response)
    }
    
    const handleLogin = async (event) => {
        console.log('handling login now!')
        const response = await loginService.login({username,password})
        console.log('Login Request sent')
        setUsername('')
        setPassword('')
        console.log(response)
    }

    const handleUChange = (event) => {
        event.preventDefault()
        setUsername(event.target.value);
    }

    const handlePChange = (event) => {
        event.preventDefault()
        setPassword(event.target.value)
    }

    const handleNameChange = (event) => {
        event.preventDefault();
        setName(event.target.value)
    }

    const handleEmailChange = (event) => {
        event.preventDefault()
        setEmail(event.target.value)
    }

    console.log('Entered into App')
    return(
        <div>
            <h1>Login</h1>
            <form onSubmit = {handleLogin}>
                <p>Username: <input type = "text" value = {username} name = "username" onChange = {handleUChange}/></p>
                <p>Password: <input type = "password" value = {password} name = "password" onChange = {handlePChange} /></p>
                <button type = "submit">Login</button>
            </form>
            <h1>Register</h1>
            <form onSubmit = {handleRegistration}>
                <p>Name: <input name = "name" value = {name} onChange = {handleNameChange}/></p>
                <p>Email: <input name = "email" value = {email} onChange = {handleEmailChange}/></p>
                <p>Password: <input name = "password" /></p>
                <p>Username: <input name = "username" /></p>
                <button type = "submit">Register</button> 
            </form>
        </div>
    )
   }


export default App